import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import PropTypes from 'prop-types'
import { Link } from "react-router-dom"
import { connect } from "react-redux"
import Breadcrumbs from "../../components/Common/Breadcrumb"
import { withTranslation } from "react-i18next"
import LeadBySource from "./lead"
import Apaexlinecolumn from "./apaexlinecolumn"
import Revnue from "../AllCharts/apex/revnueLine"
import { Table, Thead, Tbody, Tr, Th, Td } from "react-super-responsive-table"

import {
  Container,
  Row,
  Col,
  Card,
  CardBody,
  CardTitle,
  Select
} from "reactstrap"

class DashboardNew extends Component {
  constructor(props) {
    super(props)
    this.state = {
      reports: [
        { title: "Total Leads", iconClass: "bx-copy-alt", description: "14", goto: '/lead/dashboard' },
        {
          title: "Total Contacts",
          iconClass: "bx-archive-in",
          description: "6",
          goto: '/contacts'
        },
        {
          title: "Total Clients",
          iconClass: "bx-purchase-tag-alt",
          description: "5",
          goto: '/clients'
        },
        {
          title: "Forecasted sales",
          iconClass: "bx-purchase-tag-alt",
          description: "5",
          goto: '#'
        },
        {
          title: "Pipeline value",
          iconClass: "bx-purchase-tag-alt",
          description: "5",
          goto: '#'
        },
      ]
    }
  }
  render() {
    return (
      <React.Fragment>
        <div className="page-content">
          <MetaTags>
            <title>Lead Dashboard | Realty Space</title>
          </MetaTags>
          <Container fluid>
             <Breadcrumbs
              title={this.props.t("Lead")}
              breadcrumbItem={this.props.t("Dashboard")}
            />
            <Row>
            {this.state.reports.map((report, key) => (
            <Col md="3" key={"_col_" + key}>
                <Link to={report.goto}><Card className="mini-stats-wid">
                        <CardBody>
                          <div className="d-flex">
                            <div className="flex-grow-1">
                              <p className="text-muted fw-medium">
                                {report.title}
                              </p>
                              <h4 className="mb-0">{report.description}</h4>
                            </div>
                            <div className="mini-stat-icon avatar-sm rounded-circle bg-primary align-self-center">
                              <span className="avatar-title">
                                <i
                                  className={
                                    "bx " + report.iconClass + " font-size-24"
                                  }
                                />
                              </span>
                            </div>
                          </div>
                        </CardBody>
                      </Card></Link>
                
              </Col>
            ))}
            </Row>
            <Row>
              <Col md="4">
                  <Card>
                  <CardBody>
                  <div className="mb-4 card-title float-start">Activities for today</div>
                  <div className="clearfix"></div>
                  
                  </CardBody>
                  </Card>                  

              </Col>
              <Col md="4">
                  <Card>
                  <CardBody>
                  <div className="mb-4 card-title float-start">Leads received</div>
                  <div className="clearfix"></div>
                  
                  </CardBody>
                  </Card>                  

              </Col>
              <Col md="4">
                  <Card>
                  <CardBody>
                  <div className="mb-4 card-title float-start">Tasks for today</div>
                  <div className="clearfix"></div>
    
                 
                  </CardBody>
                  </Card>                  

              </Col>
             
            </Row>
            <Row>
              <Col lg="4">
                <Card>
                <CardBody>
                <div className="mb-4 card-title float-start">Sales source report</div>
                <div className="float-end">
                       <select name="" id="" className="form-control">
                         <option value="">Select Days</option>
                         <option value="">30 Days</option>
                         <option value="">60 Days</option>
                         <option value="">90 Days</option>
                         <option value="">120 Days</option>
                      </select>             

                </div>
                <div className="clearfix"></div>
                <LeadBySource />
                </CardBody>
                </Card>
              </Col>
              <Col lg="4">
                <Card>
                <CardBody>
                <div className="mb-4 card-title float-start">Sales pipeline</div>
                <div className="float-end">
                       <select name="" id="" className="form-control">
                         <option value="">Select Days</option>
                         <option value="">30 Days</option>
                         <option value="">60 Days</option>
                         <option value="">90 Days</option>
                         <option value="">120 Days</option>
                      </select>             

                </div>
                <div className="clearfix"></div>
                <Apaexlinecolumn />
               
                </CardBody>
                </Card>
              </Col>
              <Col lg="4">
                <Card>
                <CardBody>
                <div className="mb-4 card-title float-start">Revenue report</div>
                <div className="float-end">
                       <select name="" id="" className="form-control">
                         <option value="">Select Days</option>
                         <option value="">30 Days</option>
                         <option value="">60 Days</option>
                         <option value="">90 Days</option>
                         <option value="">120 Days</option>
                      </select>             

                </div>
                <div className="clearfix"></div>
                <Revnue />
                </CardBody>
                </Card>
              </Col>
            </Row>
           <Row>
             <Col lg="12">
             <Card>
                <CardBody>
                <div className="mb-4 card-title float-start">Active Leads</div>
                <div className="clearfix"></div>
                <div className="table-responsive">
  <table className="table mb-0 table">
    <thead className="table-light">
      <tr>
        <th>#</th>
        <th>Name</th>
        <th>Phone</th>
        <th>Email</th>
        <th>Last contact</th>
        <th>Source</th>
        <th>Status</th>
        <th></th>
      </tr>
    </thead>
    <tbody>
      <tr>
        <th scope="row">1</th>
        <td>Mohit Grover</td>
        <td>111-222-3333</td>
        <td>mohit@esimplified.ca</td>
	    	<td></td>
        <td></td>
        <td></td>
        <td><Link to="/lead/lead-detail" className="btn btn-primary">View Detail</Link></td>
      </tr>
     
    </tbody>
  </table>
</div>               
                </CardBody>
                </Card>                

             </Col>
           </Row>
          </Container>
        </div>
      </React.Fragment>
    )
  }
}
DashboardNew.propTypes = {
  t: PropTypes.any

}
export default connect()(withTranslation()(DashboardNew))
