import React from "react"
import { Card, CardBody, CardTitle, Badge, Button } from "reactstrap"
import { Link } from "react-router-dom"

const LatestLeads = () => {
  const leads = [
    {
      id: "customCheck2",
      leadCName: "Mohit Grover",
      phone: "111-222-3333",
      email: "mohit@esimplified.ca",
      lastContact: "",
      type:"Seller",
      leadStatus: "Open-Follow up",
      link: "#",
    },
    {
      id: "customCheck3",
      leadCName: "Vibha Grover",
      phone: "222-333-4444",

      email: "vibha@esimplified.ca",
      lastContact: "",
      type:"Both",
      leadStatus: "Open-New",
      link: "#",
    },
   
  ]

  return (
    <React.Fragment>
      <Card>
        <CardBody>
          <CardTitle className="mb-4">Active Leads</CardTitle>
          <div className="table-responsive">
            <table className="table align-middle table-nowrap mb-0">
              <thead className="table-light">
                <tr>
                  
                  <th className="align-middle">Name</th>
                  <th className="align-middle">Phone</th>
                  <th className="align-middle">Email</th>
                  
                  <th className="align-middle">Last Contact</th>
                  <th className="align-middle">Type</th>
                  <th className="align-middle">Status</th>
                 
                  <th className="align-middle">View Details</th>
                </tr>
              </thead>
              <tbody>
                {leads.map((lead, key) => (
                  <tr key={"_tr_" + key}>
                    
                    <td>
                      <Link to="#" className="text-body fw-bold">
                        {" "}
                        {lead.leadCName}{" "}
                      </Link>{" "}
                    </td>
                    <td>{lead.phone}</td>
                    <td>{lead.email} </td>
                    
                    <td>{lead.lastContact}
                     
                    </td>
                    <td>{lead.type}
                     
                    </td>
                    <td>{lead.leadStatus}
                     
                     </td>
                   
                    <td>
                    <Link to="/lead/lead-detail" className="btn-primary waves-effect waves-light btn-sm">View Details</Link>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </CardBody>
      </Card>
    </React.Fragment>
  )
}

export default LatestLeads
